import { Base } from './../../../../app/model/base';
import { UsuarioService } from './../../../service/sigobra/usuario.service';
import { Usuario } from './../../../model/objetc/usuario';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.page.html',
  styleUrls: ['./usuarios.page.scss'],
})
export class UsuariosPage implements OnInit {
  value: string = '';
  usuarios: Array<Usuario>;
  usuario: Usuario;

  constructor(private router: Router, private base: Base, private us: UsuarioService) { }

  ngOnInit() {
   
  }

  ionViewWillEnter() {
    this.pesquisar(" ");
  }

  excluir(usuarios: any) {

  }

  inserir() {
    this.us.usuario = null;
    this.router.navigate(['usuario-inserir']);
  }

  editar(usuario : Usuario) { 
    this.us.usuario = usuario;
    this.router.navigate(['usuario-inserir']);   
  }


  pesquisa(value: string) {
    this.base.present();
    return this.us.pesquisar(value).subscribe(
      data => {
        this.usuarios = data as Array<Usuario>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  usuario ' + this.base.tratarErro(error));
      }
    );
  }

  pesquisar(value: string) {
    this.base.present();
    return this.us.pesquisa(value).subscribe(
      data => {
        this.usuarios = data as Array<Usuario>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  usuario ' + this.base.tratarErro(error));
      }
    );
  }

  permissaoUsuario(usuario : Usuario) {
    this.us.flag = "01";
    this.us.usuario = usuario;
    this.router.navigate(['permissao-usuario']);
  }

  alteraSenha(usuario : Usuario) {
    this.base.atualizarSenha(usuario);
  }

}
